* {
  box-sizing: border-box;
}

.lecture {
  background: #ccc;
}

.last-row {
  border-bottom: 3px solid black;
}

td {
  font-size: 2em;
}

.empty-day {
  border-bottom: 3px solid black;
  background: gray;
}

.empty-day td:nth-child(1) {
  border-bottom: 3px solid black;
  background: white;
}

.cell {
  border: 1px solid black;
}

.place-head {
  border: 1px solid black;
}

td span {
  display: block;
}

td {
  border: 1px solid black;
}